// src/components/SwipeCards.scss
.swipe-cards {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: var(--background-color);
    overflow: hidden;

    .header {
        position: absolute;
        top: 5px;
        display: flex;
        justify-content: center;
        align-items: flex-end;

        .logoClass {
            height: 40px;
            margin-left: 5px;
            margin-right: 5px;
        }

        .word {
            height: 12px;
        }

    }

    .nav {
        position: absolute;
        bottom: 5px;
        left: 0;
        right: 0;
        height: 50px;
        display: flex;
        justify-content: space-around;
        align-items: center;

        .button {
            display: flex;
            width: 100%;
            margin: 10px;
            padding: 5px 5px;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            border: none;
            background-color: var(--input-background-color);
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
            border-radius: 10px;
            cursor: pointer;
            font-size: 12px;
            font-weight: bold;
            color: var(--input-text-color);

            svg {
                height: 20px;
                width: auto;
            }
        }
    }
}