.selfie-upload {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: var(--background-color);
    color: var(--text-color);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

    .logo {
        width: 50%;
    }

    h1 {
        margin-bottom: 1rem;
        font-size: 18px;
        color: var(--text-color);
    }

    p {
        margin-bottom: 2rem;
        font-size: 15px;
        color: var(--text-color);
        text-align: center;
    }

    form {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;

        .selfie-box {
            width: 200px;
            height: 200px;
            background-color: var(--color-grey);
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 10px;
            position: relative;
            margin-bottom: 1rem;
            border: none transparent;
            transition: border-color 0.3s ease;

            .upload-label {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                font-size: 2rem;
                color: white;
                position: relative;
                opacity: 0.5;
                border-radius: 10px;
                transition: opacity 0.3s ease;

            }

            .male-background {
                background-image: url('../../assets/images/male.jpg');
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
            }

            .female-background {
                background-image: url('../../assets/images/female.jpg');
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
            }


            .upload {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                font-size: 2rem;
                color: white;
                position: absolute;
                /* Adjust opacity as desired */
                border-radius: 10px;
                /* Rounded corners to match selfie-box */
                transition: opacity 0.3s ease;

                /* Include SVG as background */
                background-size: cover;
                background-repeat: no-repeat;

            }

            .selfie-preview {
                width: 200px;
                height: 200px;
                object-fit: cover;
                border-radius: 10px;
            }

            .upload-input {
                display: none;
            }
        }

        .submit-button {
            padding: 0.75rem 1.5rem;
            background-color: var(--button-background-color);
            color: var(--text-color);
            border: none;
            border-radius: 5px;
            cursor: pointer;
            font-size: 1rem;
            transition: background-color 0.3s ease;

        }
    }
}