.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  min-height: 100vh;
  width: 100%;

  .logo {
    width: 70%;
    margin-top: 2rem;
  }

  .welcome_description {
    font-weight: bold;
    margin-bottom: 10px;
    text-align: center;
    font-size: 15px;
    width: 95%;
  }

  .nav {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    .register-button {
      background-color: var(--color-grey);
      color: var(--color-white);
      padding: 0.75rem 1.5rem;
      border: none;
      border-radius: 26px;
      cursor: pointer;
      width: 190px;

      span {
        font-size: 14px;
      }
    }

    .language-selector-container {
      display: flex;
      justify-content: space-between;
      width: 190px;

      .language-selector {
        margin-top: 1rem;
        padding: 0.5rem;
        border-radius: 26px;
        background-color: var(--color-grey);
        color: var(--color-white);
        border: none;
        cursor: pointer;
        width: 50%;
        font-size: 14px;

      }

    }

    .them-switcher {
      margin-top: 1rem;
    }
  }

  .since {
    text-align: center;
    margin-bottom: 20px;

    p {
      font-size: 13px;
      margin: 0 auto;
    }

  }

}