.card-item {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-between;
    background-color: var(--input-background-color);
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 5px;
    /* Ensure cards stack on top of each other */



    .photo-container {
        width: 40%;
        height: 100px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10px;

        .photo {
            border-radius: 10px;
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
            cursor: pointer;
        }
    }

    .info {
        display: flex;
        width: 60%;
        justify-content: center;
        flex-direction: column;


        h2,
        h3,
        h4,
        p {
            margin: 3px 0;
            color: var(--text-color);
        }

        h2 {
            text-align: left;
            font-size: 12px;
            display: flex;
            align-items: center;

            .zodiac {
                margin-right: 5px;
                height: 10px;
            }

            .verify {
                margin-left: 5px;
                height: 10px;
            }
        }

        h3 {
            text-align: left;
            font-size: 11px;
            display: flex;
            align-items: center;

            svg {
                margin-right: 5px;
                height: 10px;
            }
        }

        h4 {
            text-align: left;
            font-size: 10px;
        }

        p {
            text-align: left;
            font-size: 9px;
            margin: 0;
        }

        .buttons-match {
            display: flex;
            justify-content: space-around;
            width: 100%;
            margin-top: 10px;

            .icon-button {

                padding: 5px 15px !important;
            }
        }

        .buttons-match {

            button {
                color: var(--color-white);
                border: none;
                border-radius: 10px;
                padding: 5px 10px;
                cursor: pointer;
                font-size: 12px;
                transition: background-color 0.3s ease;
                width: 80px;


            }

            .chat-button {
                padding: 10px 20px;
                width: 100%;
                background-color: #24A1DE;
                border-radius: 10px;
                box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
                color: var(--color-white);
                font-weight: bold;
                font-size: 12px;
                border: none;
                border-radius: 5px;
                cursor: pointer;
                transition: background-color 0.3s ease;
            }

        }
    }

}