.likes-with-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;

  .header {
    position: absolute;
    top: 5px;
    display: flex;
    justify-content: center;
    align-items: flex-end;

    .logoClass {
      height: 40px;
      margin-left: 5px;
      margin-right: 5px;
    }

    .word {
      height: 12px;
    }

  }

  h2 {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 20px;
  }

  .cards-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;

    .card-item {
      min-width: 300px;
    }
  }
}