.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    overflow: auto;
}

.popup {
    background-color: var(--background-color);
    padding: 20px;
    border-radius: 4px;
    max-width: 500px;
    width: 100%;
    position: relative;

    h2 {
        margin-bottom: 20px;
    }


    div {
        font-size: 1rem;
        color: var(--text-color);

        label {
            width: 100%;
            height: 100%;
        }

        input,
        textarea,
        select {
            width: 100%;
            padding: 0.5rem;
            margin-top: 0.5rem;
            margin-bottom: 0.5rem;
            border-radius: 5px;
            font-size: 1rem;
            border: 1.5px solid var(--input-border-color);
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
            background-color: var(--input-background-color);
            color: var(--input-text-color);
            transition: border-color 0.3s ease;

            &:focus {
                border-color: var(--button-background-color);
            }
        }

        input[type="date"] {
            appearance: none;
            -webkit-appearance: none;
            -moz-appearance: none;
            padding: 0.5rem;
            font-size: 1rem;
            background-color: var(--input-background-color);
            color: var(--input-text-color);
        }

        textarea.textArea {
            resize: vertical;
            height: auto;
        }

        input[type="radio"] {
            margin: 0 auto;
            margin-top: 10px;
            display: block;
        }
    }

    .gender {
        display: flex;
        justify-content: space-between;
        margin-bottom: 1rem;

        .gender-option {
            text-align: center;
            height: 50px;
            width: 48%;
            border-radius: 5px;
            border: 1px solid var(--input-border-color);
            background-color: var(--input-background-color);



            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            cursor: pointer;
            transition: background-color 0.3s ease, border-color 0.3s ease, transform 1s ease;
            margin-top: 10px;

            input {
                display: none;
            }

            label {
                color: var(--input-text-color);
                display: flex;
                justify-content: center;
                align-items: center;
                transition: color 0.3s ease;
            }

            input[type="radio"]:checked+label {
                background-color: var(--tg-theme-button-color); //var(--button-background-color);
                border-radius: 5px;
                font-size: larger;
                color: var(--tg-theme-button-text-color); //var(--color-white);
            }
        }
    }

    .popup-buttons {
        margin-top: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .language-selector-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;

        .language-selector {
            padding: 0.5rem;
            border-radius: 26px;
            background-color: var(--input-background-color);
            color: var(--input-text-color);
            border: none;
            cursor: pointer;
            width: 135px;
            font-size: 14px;

        }

    }

    .save-button,
    .cancel-button,
    .del-button {
        padding: 10px 20px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        width: 40%;
    }

    .save-button {
        background-color: #4CAF50;
        /* Green */
        color: white;
    }

    .cancel-button {
        background-color: var(--button-background-color);
        /* Red */
        color: white;
    }

    .del-button {
        margin-top: 10px;
        background-color: #f44336;
        width: 100%;
        color: white;
    }
}