.user-profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 20px;
    margin: 10px;
    position: relative;
    overflow: auto !important;

    .header {
        position: absolute;
        top: 5px;
        display: flex;
        justify-content: center;   
        align-items: flex-end;     

        .logoClass {
            height: 40px;
            margin-left: 5px;
            margin-right: 5px;
        }

        .word {
            height: 12px;
        }

    }


    .profile-content {
        flex: 1;
        height: 100%;
        margin-top: 10px;

        .photo-grid {
            display: grid;

            background-color: var(--input-background-color);
            border-radius: 10px;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
            margin-top: 20px;
            grid-template-columns: repeat(2, 1fr);
            height: auto;
            width: 100%;
            padding: 10px;
            gap: 1px;

            .photo-box {
                background-color: var(--color-grey);
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                transition: border-color 0.3s ease;
                width: 100%;
                height: 150px;

                &.photo-box-missing {
                    border-color: red;
                }

                .upload-label {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    font-size: 2rem;
                    color: white;
                    position: relative;

                    .photo-preview {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }

                .upload-input {
                    display: none;
                }
            }

            .photo-box-1 {
                grid-row: span 2;
                height: 301px;
            }
        }

        .profile-info {
            height: 30%;
            padding: 1rem;
            background-color: var(--input-background-color);
            border-radius: 10px;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

            .info_top {
                h3 {
                    display: flex;
                    align-items: center;

                    .verify {
                        height: 20px;
                        margin-left: 10px;
                    }

                    .zodiac {
                        margin-right: 10px;
                        height: 20px;
                    }
                }

                p {
                    display: flex;
                    align-items: center;

                    svg {
                        height: 20px;
                        margin-right: 10px;
                    }
                }
            }

            h2 {
                margin-top: 3rem;
                color: var(--text-color);
            }

            p {
                font-size: 15px !important;
                margin-top: 1rem;
                color: var(--text-color);
            }

            .title {
                margin-top: 10px;
                font-weight: bold;
            }
        }

        .counters {
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin-top: 1rem;
            margin-bottom: 1rem;
            height: 50px;

            .counter {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 80px;
                height: 80px;
                color: var(--text-color);
                transition: transform 0.3s ease, box-shadow 0.3s ease;
                cursor: pointer;

                .heartClass {
                    width: 50px;
                }

                div {
                    font-weight: bold;
                    margin-right: 5px;
                    padding: 5px 10px;
                    background-color: var(--input-background-color);
                    border-radius: 10px;
                    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
                }
            }
        }
    }

}

.edit-button {
    margin-top: 20px;
    padding: 10px 20px;
    width: 100%;
    background-color: var(--input-background-color);
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    color: var(--input-text-color);
    font-weight: bold;
    font-size: 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
        background-color: #0056b3;
    }
}

.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.popup {
    background: white;
    padding: 20px;
    border-radius: 4px;
    max-width: 500px;
    width: 100%;
    position: relative;

    h2 {
        margin-bottom: 20px;
    }

    .popup-input {
        display: block;
        width: 100%;
        padding: 10px;
        margin-bottom: 10px;
        border: 1px solid #ccc;
        border-radius: 4px;
    }

    .popup-buttons {
        display: flex;
        justify-content: space-between;
    }

    .save-button,
    .cancel-button {
        padding: 10px 20px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
    }

    .save-button {
        background-color: #4CAF50;
        /* Green */
        color: white;
    }

    .cancel-button {
        background-color: #f44336;
        /* Red */
        color: white;
    }
}