.registration-form {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: var(--background-color);
    color: var(--text-color);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;


    .header {
        position: absolute;
        top: 5px;
        display: flex;
        justify-content: center;
        align-items: flex-end;

        .logoClass {
            height: 40px;
            margin-left: 5px;
            margin-right: 5px;
        }

        .word {
            height: 12px;
        }

    }

    h2 {
        margin: 0 auto;
        margin-top: 20px;
        font-size: 18px;
        color: var(--text-color);
        text-align: center;
    }

    form {
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 400px;

        div {
            margin-top: 1rem;
            font-size: 1rem;
            color: var(--text-color);

            label {
                width: 100%;
                height: 100%;
            }

            input,
            textarea,
            select {
                width: 100%;
                padding: 0.5rem;
                margin-top: 0.5rem;
                border-radius: 5px;
                font-size: 1rem;
                border: 1.5px solid var(--input-border-color);
                box-shadow: var(--box-shadow8);
                background-color: var(--input-background-color);
                color: var(--input-text-color);
                transition: border-color 0.3s ease;

                &:focus {
                    border-color: var(--button-background-color);
                }
            }

            input[type="date"] {
                appearance: none;
                -webkit-appearance: none;
                -moz-appearance: none;
                padding: 0.5rem;
                font-size: 1rem;
                background-color: var(--input-background-color);
                color: var(--input-text-color);
            }

            textarea.textArea {
                resize: vertical;
                height: auto;
            }

            input[type="radio"] {
                margin: 0 auto;
                margin-top: 10px;
                display: block;
            }
        }

        .gender {
            display: flex;
            justify-content: space-between;
            margin-bottom: 1rem;

            .gender-option {
                text-align: center;
                height: 50px;
                width: 48%;
                border-radius: 5px;
                border: 1px solid var(--input-border-color);
                background-color: var(--input-background-color);



                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                cursor: pointer;
                transition: background-color 0.3s ease, border-color 0.3s ease, transform 1s ease;
                margin-top: 10px;

                input {
                    display: none;
                }

                label {
                    color: var(--input-text-color);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: color 0.3s ease;
                }

                input[type="radio"]:checked+label {
                    background-color: var(--tg-theme-button-color); //var(--button-background-color);
                    border-radius: 5px;
                    font-size: larger;
                    color: var(--tg-theme-button-text-color); //var(--color-white);
                }
            }
        }

        button {
            background-color: var(--button-background-color);
            color: var(--color-white);
            font-weight: bold;
            padding: 0.75rem 1.5rem;
            border: none;
            border-radius: 5px;
            cursor: pointer;
            font-size: 1rem;
            transition: background-color 0.3s ease;
            font-size: larger;

        }
    }
}