$animation-duration: 1s;
$animation-timing: ease-in-out;

.match-container {
    position: relative;
    display: flex;
    justify-content: center;
    /* Center children horizontally */
    align-items: center;
    /* Center children vertically */
    flex-direction: column;
    /* Stack children vertically */
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;

    .logo {
        position: absolute;
        top: 5px;
        height: 80px;
    }

    .logoClass {
        position: fixed;
        top: 130px;
        width: 350px;
        opacity: 0;
        /* Начальное состояние - невидимый элемент */
        animation: fadeIn 1s ease-in-out 1s forwards, heart 0.9s linear infinite 2s;
    }

    .greeting {
        position: absolute;
        top: 80px;
        margin: 0 auto;
        font-size: 30px;
        color: var(--text-color);
        margin: 0 auto;
        font-weight: bold;
        opacity: 0;
    }

    @keyframes heart {
        0% {
            transform: scale(1);
        }

        20% {
            transform: scale(1.05);
        }

        30% {
            transform: scale(1.09);
        }
    }

    @keyframes fadeIn {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }

    .user {
        position: absolute;
        top: 240px;
        transform: translateY(-50%);
        width: 150px;
        height: 150px;
        border-radius: 50%;
        overflow: hidden;
        transition: all $animation-duration $animation-timing;
        z-index: 1;
        // border: 2px #222 solid;
        box-shadow: 0 8px 40px rgb(0, 0, 0, 0.5),
            inset 0 4px 4px rgb(255, 255, 255, 0.2),
            inset 0 -4px 4px rgb(255, 255, 255, 0.2);

        .user-photo {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &.user1 {
            left: -200px;
            /* Start off-screen to the left */
        }

        &.user2 {
            right: -200px;
            /* Start off-screen to the right */
        }


        &.animate {
            &.user1 {
                left: calc(50% - 140px);
                /* Move to the left position */
            }

            &.user2 {
                right: calc(50% - 140px);
                /* Move to the right position */
            }
        }
    }

    .buttons {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: fixed;
        bottom: 30px;
        width: 80%;

        button {
            padding: 10px 20px;
            margin: 10px;
            cursor: pointer;
            transition: background-color 0.3s ease;
            width: 100%;
            background-color: var(--input-background-color);
            border-radius: 10px;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
            color: var(--input-text-color);
            font-weight: bold;
            font-size: 15px;
            border: none;
        }
    }
}