#toggle {
    position: relative;
    display: block;
    width: 60px;
    height: 30px;
    border-radius: 30px;
    background: #222;
    transition: 0.5s;
    cursor: pointer;
    box-shadow: inset 0 8px 60px rgb(0, 0, 0, 0.1),
        inset 0 8px 8px rgb(0, 0, 0, 0.1),
        inset 0 -4px 4px rgb(0, 0, 0, 0.1);

    .indicator {
        position: absolute;
        top: 0;
        left: 0;
        width: 30px;
        height: 30px;
        background: linear-gradient(to bottom, #444, #222);
        border-radius: 50%;
        transform: scale(0.9);
        box-shadow: 0 8px 40px rgb(0, 0, 0, 0.5),
            inset 0 4px 4px rgb(255, 255, 255, 0.2),
            inset 0 -4px 4px rgb(255, 255, 255, 0.2);
        transition: 0.5s;
    }

    &.active {
        background-color: var(--color-white);
        box-shadow: inset 0 2px 60px rgb(0, 0, 0, 0.1),
            inset 0 2px 8px rgb(0, 0, 0, 0.1),
            inset 0 -4px 8px rgb(0, 0, 0, 0.5);
    }

    &.active .indicator {
        left: 30px;
        background: linear-gradient(to bottom, #eaeaea, #f9f9f9);
        box-shadow: 0 8px 20px rgb(0, 0, 0, 0.1),
            inset 0 4px 4px rgb(255, 255, 255, 1),
            inset 0 -4px 4px rgb(255, 255, 255, 1);
        /* Adjust to match the width of the switch */
    }
}