*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --background-color: #f2f2f2;
    --background-color2: #f2f2f2cc;
    --text-color: #000;
    --button-background-color: #007bff;
    --input-background-color: #fff;
    --input-text-color: #000;
    --input-border-color: none;
    --color-grey: #333;
    --color-light-grey: #d3d3d3;
    --color-light-grey2: #f2f2f2;
    --color-white: #fff;
    --color-black: #000;
    --color-red: #ff0000;
    --color-green: #008000;
    --box-shadow2: rgba(0, 0, 0, 0.2);
    --box-shadow8: rgba(0, 0, 0, 0.8);
    --spinner-border: rgba(85, 84, 84, 0.479);
    --spinner-border-top: #fff;
    --box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1), 0 6px 20px rgba(0, 0, 0, 0.1); // Default shadow

}

[data-theme='dark'] {
    --background-color: #000;
    --background-color2: #00000080;
    --text-color: #fff;
    --input-background-color: #333;
    --input-text-color: #fff;
    --spinner-border: rgba(255, 255, 255, 0.3);
    --spinner-border-top: #d3d3d3;
    --box-shadow: 0 4px 8px rgba(0, 0, 0, 1), 0 6px 20px rgba(0, 0, 0, 1); // Default shadow
}

@font-face {
    font-family: 'GothamPro';
    src: url('./fonts/GothamPro.woff2') format('woff2'),
        url('./fonts/GothamPro.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
    background-color: var(--background-color);
    color: var(--text-color);
    margin: 0;
    padding: 0;
    font-family: 'GothamPro', sans-serif;
    transition: 0.5s;
    position: relative;
    -webkit-user-select: none;
    /* Safari */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}

.app {
    min-height: 100vh;
}

.route {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 300ms ease-in-out, transform 300ms ease-in-out;
}

.fade-enter {
    opacity: 1;
    transform: translateY(0);
}

.fade-exit {
    opacity: 0;
    transform: translateY(-20px);
}

.loading-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.8);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    backdrop-filter: blur(5px);
}

.loading-indicator {
    border: 4px solid rgba(0, 0, 0, 0.1);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border-left-color: var(--button-background-color);
    animation: spin 1s ease infinite;
    margin-bottom: 1rem;
}

.loading-text {
    font-size: 1.5rem;
    color: var(--text-color);
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}