.technical-works {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;

    h2 {
        /* Increase font size as desired */
        font-size: 18px;
        text-align: center;
        color: var(--input-text-color);
        /* White text color */
        margin-bottom: 20px;
        /* Spacing between text and spinner */
    }

    .logo {
        width: 50%;
    }

    .spinner {
        border: 4px solid var(--spinner-border);
        border-radius: 50%;
        border-top: 4px solid var(--spinner-border-top);
        width: 40px;
        height: 40px;
        animation: spin 1s linear infinite;
    }

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }

        100% {
            transform: rotate(360deg);
        }
    }
}