@keyframes swipeLeft {
  from {
    transform: translateX(0) rotate(0deg);
  }

  to {
    transform: translateX(-100%) rotate(-20deg);
    opacity: 0;
  }
}

@keyframes swipeRight {
  from {
    transform: translateX(0) rotate(0deg);
  }

  to {
    transform: translateX(100%) rotate(20deg);
    opacity: 0;
  }
}

@keyframes superSwipeRight {
  from {
    transform: translateX(0) rotate(0deg);
  }

  to {
    transform: translateX(100%) rotate(20deg) scale(0.5);
    opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes reverseFadeIn {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(0.95);
  }
}


@keyframes reverseSwipeLeft {
  from {
    transform: translateX(-100%) rotate(-20deg) scale(0.5);
    opacity: 0;
  }

  to {
    transform: translateX(0) rotate(0deg);
    opacity: 1;
  }
}

.swipe-left {
  animation: swipeLeft 0.4s ease-out forwards;
}

.swipe-right {
  animation: swipeRight 0.4s ease-out forwards;
}

.super-swipe-right {
  animation: superSwipeRight 0.4s ease-out forwards;
}

.fade-in {
  animation: fadeIn 0.4s ease-in;
}

.reverse-fade-in {
  animation: reverseFadeIn 0.4s ease-in;
}

.reverse-swipe-left {
  animation: reverseSwipeLeft 0.4s ease-out forwards;
}


.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: calc(100vh - 120px);
  background-color: var(--input-background-color);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 10px;
  position: absolute;
  width: calc(100% - 20px);
  /* Ensure cards stack on top of each other */

  .photo-container {
    // width: 280px;
    // height: 280px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;

    .photo {
      border-radius: 10px;
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
      cursor: pointer;
    }
  }



  .dots-container {
    display: flex;
    justify-content: center;
    margin: 10px 0;

    .dot {
      height: 10px;
      width: 10px;
      margin: 0 5px;
      background-color: #bbb;
      border-radius: 50%;
      display: inline-block;
    }


    .light.active {
      background-color: #333;
    }

    .dark.active {
      background-color: #fff;
    }
  }


  h2,
  h3,
  p {
    margin: 3px 0;
  }

  h2 {
    text-align: left;
    font-size: 15px;
    display: flex;
    align-items: center;

    .zodiac {
      margin-right: 5px;
      height: 20px;
    }

    .verify {
      margin-left: 5px;
      height: 20px;
    }
  }

  h3 {
    text-align: left;
    font-size: 14px;
    display: flex;
    align-items: center;

    svg {
      margin-right: 5px;
      height: 20px;
    }
  }

  h4 {
    text-align: left;
    font-size: 13px;
  }

  p {
    text-align: left;
    font-size: 12px;
    margin: 0;
  }


  .buttons {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 10px;

  }
}