.photo-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--box-shadow8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    animation: fadeIn 0.2s ease-in-out;

    .photo-modal-content {
        height: 90%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        // max-width: 600px;
        animation: slideIn 0.2s ease-in-out;

        .modal-photo {
            width: 100%;
            padding: 10px;
            border-radius: 15px;
            transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
            opacity: 0;
            transform: translateX(0);

            &.loaded {
                opacity: 1;
                max-height: 90%;

            }

            &.swipe-left {
                transform: translateX(-100%);
            }

            &.swipe-right {
                transform: translateX(100%);
            }
        }

        .photo-dots {
            display: flex;
            justify-content: center;
            gap: 10px;
            margin-top: 10px;

            span {
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: var(--color-white);
                opacity: 0.5;
                cursor: pointer;
                transition: opacity 0.2s ease-in-out;

                &.active {
                    opacity: 1;
                }
            }
        }
    }

    .close-button {
        padding: 10px 20px;
        width: 80%;
        background-color: var(--input-background-color);
        border-radius: 10px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        color: var(--input-text-color);
        font-weight: bold;
        font-size: 12px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.3s ease;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slideIn {
    from {
        transform: translateY(-20px);
    }

    to {
        transform: translateY(0);
    }
}