.photo-upload {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    background-color: var(--background-color);
    color: var(--text-color);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    position: relative;

    .logo {
        width: 50%;
    }

    h1 {
        font-size: 18px;
        color: var(--text-color);
    }

    form {
        width: 100%;
        height: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .photo-grid {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 5px;
            width: 100%;

            .photo-box {
                background-color: var(--color-grey);
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                border: 2px solid transparent;
                transition: border-color 0.3s ease;
                width: 100%;
                height: 150px;

                &.photo-box-missing {
                    border-color: red;
                }

                .upload-label {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    font-size: 2rem;
                    color: white;
                    position: relative;

                    .photo-preview {
                        max-width: 100%;
                        max-height: 100%;
                        object-fit: contain;
                        border-radius: 5px;
                    }
                }

                .upload-input {
                    display: none;
                }
            }

            .photo-box-1 {
                height: 305px;
                grid-row: span 2;
            }
        }

        .submit-button {
            margin-top: 2rem;
            padding: 0.75rem 1.5rem;
            background-color: var(--button-background-color);
            color: var(--text-color);
            border: none;
            border-radius: 5px;
            cursor: pointer;
            font-size: 1rem;
            transition: background-color 0.3s ease;
        }
    }


}