.verification-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: var(--background-color);
    color: var(--text-color);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

    .logo {
        width: 50%;
    }

    h1 {
        width: 100%;
        margin-bottom: 2rem;
        font-size: 18px;
        color: var(--text-color);
        text-align: center;
    }

    p {
        margin-bottom: 2rem;
        font-size: 15px;
        color: var(--text-color);
        text-align: center;
    }

    .verification-button {
        padding: 0.75rem 1.5rem;
        background-color: var(--button-background-color);
        color: var(--text-color);
        border: none;
        border-radius: 5px;
        cursor: pointer;
        font-size: 1rem;
        transition: background-color 0.3s ease;

    }
}